<template>
  <div :style='{height: ht + "px"}' style='position: relative; width: 100%;'>
    <img src="../assets/login_bg@2x.png" style='width: 100%; height: 99.5%;'/>
    <div style='position: absolute; top: 0px; left: 0px; width: 100%; text-align: center;'>
      <van-icon @click='$router.push("/mine")' name="arrow-left" color='#fff' size='40'
                style='margin-top: 20px; margin-left: 10px; float: left;'/>
      <div style='width: 90%; margin-top: 18%; height: auto; display: inline-block; box-sizing: border-box; padding: 40px 20px; text-align: left;'>
        <div style='color:rgba(250, 84, 28, 1); font-weight: bold; font-size: 30px;'>登陆</div>
        <div class='inputBg'>
          <img src="../assets/shouji@2x.png"/>
          <input v-model="phone" placeholder='请输入您的手机号码'/>
        </div>
        <div class='inputBg'>
          <img src="../assets/mima@2x.png"/>
          <input v-model="pass" type="password" placeholder='请输入您的登陆密码'/>
        </div>

        <div style='color: rgba(250, 84, 28, 1); width: 100%; height: 30px; margin-top: 26%; margin-bottom: 8px;'>
          <span style='float: left;' @click="$router.push('/zhuce')">没有账号？立即注册</span>
          <span style='float: right;'>忘记密码</span>
        </div>
        <div style='width: 100%; height: 52px; font-size: 22px; text-align: center;line-height: 52px; border-radius: 30px; color: #fff; background:linear-gradient(90deg,rgba(255,142,106,1) 0%,rgba(250,84,28,1) 100%);' @click="login">
          登陆
        </div>

      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "login",
    data() {
      return {
        ht: window.screen.height,
        phone: '',
        pass: '',
      }
    },
    mounted() {

    },
    methods: {
      async login(){
        if(!this.phone){
          this.$toast('请输入手机号码');
          return;
        }
        if(!this.pass){
          this.$toast('请输入密码');
          return;
        }
        const {err,data} = await this.$service.login.doLogin({
          username: this.phone,
          password: this.pass,
        })
        if(err){
          this.$toast(err.message);
          return;
        }
        console.log(data)
        sessionStorage.setItem('token', data.token)
        sessionStorage.setItem('user',JSON.stringify(data.user))
        this.$router.push('/');
      }
    }
  }
</script>

<style scoped>
  .inputBg {
    width: 100%;
    height: 52px;
    margin-top: 15px;
    line-height: 52px;
    border-radius: 30px;
    background: rgba(247, 247, 247, 1);
  }

  .inputBg img {
    width: 30px;
    float: left;
    margin: 11px 20px;
  }

  .inputBg input {
    float: left;
    height: 50px;
    background: none;
    color: rgba(191, 191, 191, 1);
    border: none;
    font-size: 18px;
  }
</style>